<template>
  <div class="hs-container">
    <c-header :title="$t('base.ForgetPassword')" isBackNav></c-header>
    <hs-section>
      <hs-cell-1 @click.native="$router.push('/forgetPasswordStep1')">
        <div class="custom-cell">
          <div class="logo" style="background-image:url('static/image/login/forget-password/phone.png')"></div>
          <div class="details">
            <p>{{$t('base.ByPhoneNumber')}}</p>
            <p>{{$t('base.PasswordCanBeResetViaPhone')}}</p>
          </div>
        </div>
      </hs-cell-1>
      <hs-cell-1 @click.native="$router.push('/forgetPasswordEmail')">
        <div class="custom-cell">
          <div class="logo" style="background-image:url('static/image/login/forget-password/email.png')"></div>
          <div class="details">
            <p>{{$t('base.ByMail')}}</p>
            <p>{{$t('base.PasswordCanBeResetViaEmail')}}</p>
          </div>
        </div>
      </hs-cell-1>
    </hs-section>
  </div>
</template>
<script type="text/ecmascript-6">
import CHeader from 'components/c-header/c-header'
import HsSection from 'base/hs-tableview/hs-section/hs-section'
import HsCell1 from 'base/hs-tableview/hs-cell-1/hs-cell-1'
export default {
  components: {
    CHeader,
    HsSection,
    HsCell1
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  .custom-cell
    @extend .flex-row
    @extend .flex-start
    width 100%
    padding 10px 0
    >.logo
      width 30px
      height 30px
      background-size 100% 100%
      margin-right 10px
    >.details
      width calc(100% - 40px)
      >p
        &:nth-child(1)
          color $color_black_text_1
          font-weight bold
        &:nth-child(2)
          color $color_gray_text
          font-size 0.9rem
          margin-top 5px
          line-height 1.2em
</style>



